import styled from "vue-styled-components";

const CardTitle = styled.h4`
    font-size: 1.2rem;
    font-weight: 500;
    max-height: 3rem;
    max-width: 100%;
    background-color: inherit;
    height: 3%;
    margin: .2rem;

    white-space:nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
`;

